
import Selector from 'vux/src/components/selector/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import ChinaAddressV3Data from 'vux/src/datas/china_address_v3.json'
import XAddress from 'vux/src/components/x-address/index.vue'
import Value2nameFilter from 'vux/src/filters/value2name.js'

import { mapState } from 'vuex'
import $ from 'jquery'
import event from '../../../js/Event.js'
import { AjaxHttp } from '../../../js/ajaxHttp'
import PlateType from '../../../js/enum.js'
import { getReceivablesAddInfo, getReceivingorderAddInfo, getReceivablesEditInfo, getReceivingorderEditInfo } from '../data/financeData.js'
import { isArrayType, objToString, isPostNullValue, getYMDHMFormateTimestamp, getYMDFormateTimestamp, copyArr, getModuleKeyword } from "../../../js/utility.js"

export default {
name: 'financeadd',
components: {
XTextarea,
XAddress,
ChinaAddressV3Data,
Selector,
XInput,
Group,
Datetime,
Checker,
CheckerItem
},
data() {
return {
addressData: ChinaAddressV3Data,
detail: {}, //添加回款需要的信息
list: [],
}
},
created() {},
beforeDestroy() {
event.$off("saveDone");
},
beforeRouteLeave(to, from, next) {

if (to.name === 'relatelist' || to.name === 'selectlist') {
if (to.name === 'relatelist') {
event.$on('selectedRelateItem', function(data) {
this.handleRelatedInfo(data);
}.bind(this));
} else if (to.name === 'selectlist') {
event.$on('selectedRelateItem', function(data) {
this.handleSelectedInfo(data);
}.bind(this));

}

this.addFinanceInfos.type = 'financeadd' + this.$route.params.type;
this.addFinanceInfos.list = this.list;
this.addFinanceInfos.detail = this.detail;
this.$store.commit('updateAddFinanceInfos', this.addFinanceInfos);
} else {
this.$store.commit('updateAddFinanceInfos', {});
}
next();
},
beforeRouteEnter(to, from, next) {
if (from.name === 'relatelist' || to.name === 'selectlist') {
event.$off("selectedRelateItem");
}
next();
},
mounted() {
if (this.addFinanceInfos.type === 'financeadd' + this.$route.params.type) {
this.detail = this.addFinanceInfos.detail;
this.list = this.addFinanceInfos.list;
} else {
this.getHandleInfo();
}
event.$on('saveDone', function() {
this.saveInfos();
}.bind(this));
},
computed: {
...mapState([
'userInfo',
'addFinanceInfos',
'crmShowDetail',
'crmListInfo',
])
},
methods: {
getHandleInfo() {
if (this.$route.params.type === PlateType.FinanceReceivables) { //添加应收款
if (this.$route.params.action === 'default') {
AjaxHttp('POST', 'm=finance&a=getCode', { //获取应收款编号
t: 'receivables'
}, this, function(res, wself, success) {
if (success) {
wself.list = getReceivablesAddInfo(res.data, wself.userInfo);
}
});
} else if (this.$route.params.action === 'edit') {
this.list = getReceivablesEditInfo(this.crmShowDetail.resData.data);
}

} else if (this.$route.params.type === PlateType.FinanceReceivingorder) { //添加回款

AjaxHttp('POST', 'm=finance&a=getCode', {
t: 'receivingorder'
}, this, function(res, wself, success) {
if (success) {

var data = [];
for (var i = 0; i < res.list.length; i++) {
var item = res.list[i];

data.push({ key: item.account_id, value: item.open_bank });
}
wself.detail = res.list;
if (wself.$route.params.action === 'default') {
wself.list = getReceivingorderAddInfo(data, wself.userInfo);
} else if (wself.$route.params.action === 'edit') {
wself.list = getReceivingorderEditInfo(data, wself.crmShowDetail.resData.data);
}

}
});


}
},
saveInfos() {
var add = true;
var postParams = {};

for (var i = 0; i < this.list.length; i++) {
var item = this.list[i];
var fieldValue = '';
if (item.form_type === 'text' || item.form_type === 'number' || item.form_type === 'floatnumber' || item.form_type === 'email' || item.form_type === 'mobile' || item.form_type === 'textarea') {
if (item.disabled) {
fieldValue = item.fieldvalue;
} else {
fieldValue = item.content;
}
} else {
fieldValue = item.fieldvalue;
}

if (item.is_null === '1' && isPostNullValue(fieldValue)) {
this.$vux.toast.text(item.name + '是必填项', 'top');
add = false;
break;
}
if (item.form_type === 'datetime' && !isPostNullValue(fieldValue)) {
if (item.data === 'YYYY-MM-DD HH:mm') {
fieldValue = getYMDHMFormateTimestamp(fieldValue);
} else if (item.data === 'YYYY-MM-DD') {
fieldValue = getYMDFormateTimestamp(fieldValue);
}
} else if (item.form_type === 'address') {
var addresstempvalue = Value2nameFilter(fieldValue, ChinaAddressV3Data);
var addressfieldvalue = addresstempvalue === '' ? [] : addresstempvalue.split(" ");
if (isPostNullValue(addressfieldvalue)) {
addressfieldvalue = addressfieldvalue.concat(['', '', '']);
}
addressfieldvalue.push(isPostNullValue(item.addition) ? '' : item.addition);
fieldValue = JSON.stringify(addressfieldvalue);
} else if (item.form_type === 'checkbox') {
fieldValue = JSON.stringify(fieldValue);
}
postParams[item.field] = fieldValue;

}
if (add) {
this.requestHandle(postParams);
}
},
requestHandle(postParams) {

var url = '';

if (this.$route.params.action === 'default') {
url = "m=finance&a=add";
} else if (this.$route.params.action === 'edit') {
url = "m=finance&a=edit";
postParams['id'] = this.$route.params.id;
}
if (this.$route.params.type === PlateType.FinanceReceivables) { //添加应收款
postParams['t'] = 'receivables';
} else if (this.$route.params.type === PlateType.FinanceReceivingorder) { //添加回款
postParams['t'] = 'receivingorder';
}
AjaxHttp('POST', url, postParams, this, function(res, wself, success) {
if (success) {
if (wself.$route.params.action === 'edit') { //编辑完成刷新详情信息 防止重新编辑出错
wself.crmShowDetail.refresh = true;
wself.$store.commit('updateCrmShowDetail', wself.crmShowDetail);
}

if (wself.$route.params.action === 'default') { //如果是添加页面
wself.crmListInfo.topRefresh = true;
wself.$store.commit('updateCrmListInfo', wself.crmListInfo);
}
wself.$vux.toast.text(res.info, 'top');
wself.$router.go(-1);
}
});
},
onRelateClick(item, row) { //相关跳转列表
if (item.form_type === 'contract') { //合同关联
this.$router.push({
name: 'relatelist',
params: {
type: PlateType.Contract,
fromtype: this.$route.params.type,
action: 'select',
section: '0',
row: row.toString(),
addition: '-1'
}
});
} else if (item.form_type === 'customer') {
if (this.$route.params.type === PlateType.FinanceReceivables && item.fieldvalue === '') {
this.$vux.toast.text('请选择相关合同进行客户的关联', 'top');
}
} else if (item.form_type === 'receivables') { //商机
this.$router.push({
name: 'relatelist',
params: {
type: PlateType.FinanceReceivables,
fromtype: this.$route.params.type,
action: 'select',
section: '0',
row: row.toString(),
addition: '-1'
}
});
} else if (item.form_type === 'user') {
this.$router.push({
name: 'selectlist',
params: {
id: '-1',
type: this.$route.params.type,
selecttype: '2',
section: '-1',
row: row.toString(),
}
});
}
},
handleRelatedInfo(data) { //相关列表数据处理
if (data.type === PlateType.Contract) {
var item = this.list[0];
item.content = data.item.title;
item.fieldvalue = data.item.id;
this.list[0] = item;
var customeritem = this.list[1];
customeritem.content = data.item.customer_name;
customeritem.fieldvalue = data.item.customer_id;
this.list[1] = customeritem;
} else if (data.type === PlateType.FinanceReceivables) { //应收款列表
var item = this.list[0];
item.content = data.item.name;
item.fieldvalue = data.item.receivables_id;
this.list[0] = item;
}
},
handleSelectedInfo(data) {
if (data.type === '2') { //负责人选择
var row = parseInt(data.row);
var item = this.list[row];
item.content = data.item.user_name;
item.fieldvalue = data.item.role_id;
this.list[row] = item;
}
},
onChange(val) {
if (this.$route.params.type === PlateType.FinanceReceivingorder) {
for (var i = 0; i < this.detail.length; i++) {
var item = this.detail[i];
if (item.account_id === val) {
var accountItem = this.list[2];
accountItem.fieldvalue = item.bank_account;
accountItem.content = item.bank_account;
this.list[2] = accountItem;
var companyItem = this.list[3];
companyItem.fieldvalue = item.company;
companyItem.content = item.company;
this.list[3] = companyItem;
}
}
}
}
}
}




